    return (
        <Router>
            <div style={{ padding: '20px', fontFamily: 'Arial, sans-serif' }}>
                <h1>Информация о домах</h1>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="/house/:id" element={<HouseCard />} />
                </Routes>
                <h2><a href="/">Menu</a></h2>
            </div>
        </Router>
    );
